.workex-section {
  background-color: #f8f9fa;
  padding: 50px 20px;
  font-family: "Segoe UI", sans-serif;
}

.workex h2 {
  font-size: 2rem;
  font-weight: bold;
  color: #138781;
  margin-bottom: 10px;
}

.workex hr {
  border: 2px solid #138781;
  width: 150px;
  margin: 10px auto 30px auto;
}

.timeline {
  position: relative;
  margin: 40px auto;
  padding-left: 40px;
  border-left: 4px solid #138781;
  max-width: 800px;
}

.timeline-item {
  position: relative;
  margin-bottom: 40px;
  animation: slideUp 0.6s ease forwards;
  opacity: 0;
  transform: translateY(20px);
}

.timeline-dot {
  position: absolute;
  left: -13px;
  top: 10px;
  height: 20px;
  width: 20px;
  background-color: #138781;
  border-radius: 50%;
  border: 3px solid white;
  z-index: 1;
}

.timeline-content {
  padding: 20px 25px;
  border-radius: 10px;
  background: #e9f0fb;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.timeline-content:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.15);
}

.timeline-year {
  font-weight: bold;
  color: #333;
  font-size: 14px;
  display: block;
  margin-bottom: 8px;
}

.timeline-content h3 {
  margin: 5px 0;
  font-size: 18px;
  color: #138781;
}

.timeline-content h4 {
  margin: 0;
  font-size: 16px;
  color: #555;
}

.timeline-content p {
  margin-top: 10px;
  font-size: 15px;
  color: #444;
  line-height: 1.5;
}

@keyframes slideUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
/* ======================= */
@media (max-width: 600px) {
  .work {
    margin: 0;
    padding: 0;
    margin-top: 50px;
  }
  .work hr {
    width: 280px;
  }
}
