.contact-section {
  width: 100%;
  padding: 20px 0;
  background-color: #f7f7f7;
  display: flex;
  justify-content: center;
}

.contact-box {
  width: 85%;
  max-width: 1500px;
  border: 2px solid #ddd;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: white;
  border-radius: 10px;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  min-height: 300px;
}

.contact-img {
  width: 400px;
  height: 100%;
  max-height: 350px;
  object-fit: cover;
}

.contact-form {
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 8px 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 14px;
  resize: none;
  outline: none;
  margin-bottom: 12px;
}

.contact-form button {
  padding: 8px;
  background-color: #0d6efd;
  border: none;
  color: white;
  font-size: 14px;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.contact-form button:hover {
  background-color: #0b5ed7;
}

.line {
  height: 1px;
  background-color: #ccc;
  flex: 1;
  margin: 0 8px;
}

.or {
  font-size: 20px;
  color: #888;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (max-width: 768px) {
  .contact-box {
    flex-direction: column;
    width: 90%;
    min-height: auto;
  }

  .contact-form {
    padding: 16px;
  }

  .contact-img {
    max-height: 200px;
  }
}
/* ========================= */
@media (max-width: 600px) {
  .contact {
    margin: 0;
    padding: 0;
    margin-top: 50px;
  }
  .contact .card2 {
    margin: 0;
  }
  .contact .card2 h6 {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
