.home-container {
  height: 300px;
  color: white;
  padding-left: 150px;
  padding-top: 50px; /* added */
}

.home-content h2 {
  padding: 5px;
  padding-top: 30px; /* reduced */
}
.home-content h1 {
  color: #e0b50f;
  padding: 5px;
}

.home-buttons {
  margin-top: 30px;
}

.home-buttons .btn-hire {
  background-color: #3b8ff3;
  color: white;
  width: 160px;
  padding: 10px 20px;
  border-radius: 8px; /* added */
  text-align: center;
  text-decoration: none;
  display: inline-block;
}

.home-buttons .btn-hire:hover {
  background-color: white;
  color: #3b8ff3;
  width: 200px;
  transition: 0.5s;
}

.home-buttons .btn-cv {
  background-color: #f29f67;
  color: white;
  margin-left: 15px;
  padding: 10px 20px;
  border-radius: 8px; /* added */
  text-align: center;
  text-decoration: none;
  display: inline-block;
}

.home-buttons .btn-cv:hover {
  opacity: 0.8;
  transition: 0.5s;
}

/* Theme toggle button */
.theme-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  width: 50px;
  background-color: #1e1e1e;
  margin-top: 20px;
  text-align: center;
  position: fixed;
  right: 0;
  cursor: pointer;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  z-index: 1;
}

/* Responsive */
@media (max-width: 600px) {
  .home-container {
    padding: 0;
    margin: 0;
    margin-top: 50px;
  }
  .home-container h2,
  .home-container h1 {
    font-size: 22px;
    text-align: center;
  }
  .home-buttons {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .home-buttons .btn-hire,
  .home-buttons .btn-cv {
    width: 80%;
    margin-bottom: 10px;
  }
}
