.education {
  padding-left: 200px;
  margin: 60px 0px;
  text-align: center;
}

.education hr {
  color: #138781;
  width: 200px;
  margin: 10px auto;
}

.education h2 {
  font-weight: bold;
  color: #138781;
  font-size: 2rem;
}

.education-section .education h2 {
  color: #138781 !important;
  font-weight: bold;
  font-size: 2rem;
}

.education-section {
  background-color: #f5f5f5;
  padding: 40px 0;
}

.timeline {
  position: relative;
  margin: 30px auto;
  padding: 10px 0;
  max-width: 600px;
}

.timeline::before {
  content: "";
  position: absolute;
  left: 30px;
  top: 0;
  bottom: 0;
  width: 2px;
  background: #007bff;
}

.timeline-item {
  position: relative;
  margin-bottom: 30px;
  padding-left: 60px;
}

.timeline-dot {
  position: absolute;
  left: 22px;
  top: 5px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #007bff;
  border: 2px solid white;
}

.timeline-content {
  background: white;
  padding: 15px 20px;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.timeline-year {
  font-weight: bold;
  color: #007bff;
}
.education-section {
  background-color: #f8f9fa;
  padding: 50px 20px;
  font-family: "Segoe UI", sans-serif;
}

.education h2 {
  font-size: 2rem;
  font-weight: bold;
  color: #2c3e50;
}

.timeline {
  position: relative;
  margin: 40px auto;
  padding-left: 40px;
  border-left: 4px solid #3498db;
  max-width: 800px;
}

.timeline-item {
  position: relative;
  margin-bottom: 40px;
  animation: slideUp 0.6s ease forwards;
  opacity: 0;
  transform: translateY(20px);
}

.timeline-item:nth-child(even) .timeline-content {
  background: #e9f0fb;
  border-left: 4px solid #0d6efd;
}

.timeline-item:nth-child(odd) .timeline-content {
  background: #eefaf2;
  border-left: 4px solid #2ecc71;
}

.timeline-dot {
  position: absolute;
  left: -13px;
  top: 10px;
  height: 20px;
  width: 20px;
  background-color: #3498db;
  border-radius: 50%;
  border: 3px solid white;
  z-index: 1;
}

.timeline-content {
  padding: 20px 25px;
  border-radius: 10px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.timeline-content:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.15);
}

.timeline-year {
  font-weight: bold;
  color: #333;
  font-size: 14px;
  display: block;
  margin-bottom: 8px;
}

.timeline-content h3 {
  margin: 5px 0;
  font-size: 18px;
  color: #0d6efd;
}

.timeline-content h4 {
  margin: 0;
  font-size: 16px;
  color: #555;
}

.timeline-content p {
  margin-top: 10px;
  font-size: 15px;
  color: #444;
  line-height: 1.5;
}

@keyframes slideUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
/* ========================== */
@media (max-width: 600px) {
  .education {
    margin: 0;
    padding: 0;
    margin-top: 30px;
  }
  .education h2 {
    padding-top: 20px;
  }
  .education hr {
    width: 280px;
    margin-bottom: 10px;
  }
}
