.navbar-profile-pic img {
  border-radius: 80px;
  height: 130px;
  width: 130px;
  display: flex;
  vertical-align: middle; /* âœ… Fixed */
  margin: 0 auto;
  margin-top: 20px;
}
.nav-items {
  color: white;
  margin-top: 30px;
  /* text-align: center; */
  margin-left: 25px;
}
.nav-link {
  margin-top: 20px;
  cursor: pointer;
}
.nav-link svg {
  margin-right: 10px;
}
.nav-link:hover {
  color: #4a90e2;
}
.nav-link .active {
  color: #4a90e2;
  border-left: 5px solid #f29f67;
  padding-left: 10px;
  text-decoration: none;
}
