.project {
  padding-left: 200px;
  margin: 60px 0px;
  text-align: center;
}

.project hr {
  color: #138781;
  width: 200px;
  margin: 10px auto;
}

.project h2 {
  font-weight: bold;
  color: #138781;
}

#ads {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
  padding: 10px 20px;
}

#ads .col-md-4 {
  flex: 0 0 300px;
  max-width: 300px;
}

.card {
  position: relative;
  width: 300px;
  border: 1px solid #ddd;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  background-color: #fff;
  transition: transform 0.3s ease-in-out;
}

.card:hover {
  transform: translateY(-5px);
}

.card-image {
  position: relative;
}

.card img {
  height: 180px;
  width: 100%;
  object-fit: cover;
}

.card-notify-badge {
  position: absolute;
  left: 10px;
  top: 10px;
  background: #f29f67;
  text-align: center;
  border-radius: 20px;
  color: black;
  padding: 5px 10px;
  font-size: 13px;
  font-weight: bold;
}

.card-image-overly {
  margin-top: 10px;
}

.card-detail-badge {
  display: inline-block;
  background-color: #e0f7f7;
  color: #333;
  border-radius: 20px;
  padding: 5px 12px;
  margin: 5px;
  font-size: 12px;
}

.card-body {
  padding: 15px;
}

.ad-title h5 {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 10px;
}

.ad-btn {
  text-decoration: none;
  color: #fff;
  background-color: #138781;
  padding: 8px 16px;
  border-radius: 5px;
  display: inline-block;
  margin-top: 10px;
  font-size: 14px;
}

.ad-btn:hover {
  background-color: #0f6f65;
}
/* ================================== */
@media (max-width: 600px) {
  .project {
    margin: 0;
    padding: 0;
    margin-top: 50px;
  }
  .project hr {
    width: 280px;
  }
  .project .card {
    margin-top: 30px;
  }
}
