.Techstack {
  padding-left: 130px;
  margin-top: 60px 0px;
}
.Techstack hr {
  color: #138781;
  width: 200px;
  margin: 10px auto;
}
.Techstack h2 {
  font-weight: bold;
  color: #138781;
}
.Techstack .card {
  cursor: pointer;
}
.Techstack .tech-icon {
  font-size: 2rem;
  margin-right: 10px;
}
.Techstack .card :hover {
  background-color: #138781;
  color: white;
  border: none;
  transition: 1s;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}
/* ============================ */
@media (max-width: 600px) {
  .Techstack {
    padding: 0;
    margin: 0;
    margin-top: 30px;
  }
  .Techstack hr {
    widht: 280px;
  }
}
